import React, { useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
// import { useLocation } from "react-router-dom";
import {
  useGetOneMentorDetail,
  useGetOneMentorMenteeList,
} from "../../../../hooks/member_tab/department-api";
import MentorCard from "../DepartmentHead/DepartmentMentor/Mentor/MentorCard";
import StaffMenteeCard from "./StaffMenteeCard";
import QvipleLoading from "../../../../Loader/QvipleLoading";
import AddButtonWrapper from "../../../../optimized/Ui/Button/AddButtonWrapper";
import CreateButton from "../../../../optimized/Ui/Button/CreateButton";
import { useTranslation } from "react-i18next";
import MentorStudentMenu from "../DepartmentHead/DepartmentMentor/DepartmentMentorDetail/MentorStudentMenu";
import AddStudent from "../DepartmentHead/DepartmentMentor/DepartmentMentorDetail/AddStudent";
const StaffMenteeList = ({ mid, departmentId }) => {
  const { t } = useTranslation();
  const [ref, inView] = useInView();
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [showingDataLoading, setShowingDataLoading] = useState(true);
  const [studentList, setStudentList] = useState([]);
  const [add, setAdd] = useState(false);
  const [openMenteeMenu, setOpenMenteeMenu] = useState("");

  const { getOneMentorDetail, getOneMentorDetailRefetch } =
    useGetOneMentorDetail({
      mid: mid,
      skip: !mid,
    });

  useEffect(() => {
    if (mid) {
      getOneMentorDetailRefetch();
    }
  }, [mid, getOneMentorDetailRefetch]);
  const { getOneMentorMenteeList, getOneMentorMenteeListRefetch } =
    useGetOneMentorMenteeList({
      data: {
        mid: mid,
        page: page,
        limit: 10,
      },
      skip: !mid,
    });
  useEffect(() => {
    if (mid) {
      setShowingDataLoading(true);
      getOneMentorMenteeListRefetch();
    }
  }, [mid, page, getOneMentorMenteeListRefetch]);

  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);

  useEffect(() => {
    if (getOneMentorMenteeList?.all_mentees) {
      if (page === 1) {
        setStudentList(getOneMentorMenteeList?.all_mentees);
      } else {
        setStudentList((prevState) =>
          [
            ...new Set(
              [...prevState, ...getOneMentorMenteeList?.all_mentees]?.map(
                JSON.stringify
              )
            ),
          ]?.map(JSON.parse)
        );
      }

      setShowingDataLoading(false);
    } else if (getOneMentorMenteeList?.all_mentees?.length === 0)
      setShowingDataLoading(false);
    else {
    }
    if (getOneMentorMenteeList?.all_mentees?.length === 10) setState(true);
    else setState(false);
  }, [getOneMentorMenteeList?.all_mentees]);

  const onClose = () => {
    setAdd((pre) => !pre);
  };
  const onRefetchWhenCreate = () => {
    setPage(1);
    setShowingDataLoading(true);
    getOneMentorMenteeListRefetch();
  };

  return (
    <>
      <AddButtonWrapper>
        <CreateButton
          label={t("add_new_mentee")}
          customStyle={{
            margin: "0",
            marginBottom: "0.5rem",
          }}
          onAction={onClose}
        />
      </AddButtonWrapper>

      <MentorCard viewAs="MENTOR_DETAIL" mentor={getOneMentorDetail?.mentor} />
      {studentList?.map((mentee, index) =>
        studentList?.length === index + 1 ? (
          <div ref={ref} key={mentee?._id}>
            <StaffMenteeCard
              mentee={mentee}
              setOpenMenteeMenu={setOpenMenteeMenu}
              isAccess
            />
          </div>
        ) : (
          <StaffMenteeCard
            mentee={mentee}
            key={mentee?._id}
            setOpenMenteeMenu={setOpenMenteeMenu}
            isAccess
          />
        )
      )}
      {showingDataLoading && <QvipleLoading />}
      {add && (
        <AddStudent
          onClose={onClose}
          did={departmentId}
          mid={mid}
          onRefetch={onRefetchWhenCreate}
        />
      )}
      {openMenteeMenu && (
        <MentorStudentMenu
          onClose={() => setOpenMenteeMenu("")}
          did={departmentId}
          mid={mid}
          onRefetch={onRefetchWhenCreate}
          openMenteeMenu={openMenteeMenu}
        />
      )}
    </>
  );
};

export default StaffMenteeList;
